import { BiPluginConfig } from '../../types';
import { cardFactory, CustomizedBiCardPluginType } from '@ali/4ever-factory';
import locale from '../../locales/zh_CN';
import { VoteCardPlugin } from '@ali/4ever-bamboo';
import createOnCangjieCopy from './handlers/createOnCangjieCopy';
import createRenderNode from './createRenderNode';
import createMenubar from './handlers/createMenu';
import { copyNodes } from '@ali/4ever-plugin-clipboard'

const { createOnAction, createQueries } = VoteCardPlugin;

const createPlugin = (bizConfig: BiPluginConfig): CustomizedBiCardPluginType => {
  const voteCardConfig = {
    ...locale.voteCard,
    ...bizConfig.voteCard,
  };

  // @ts-ignore
  const renderNode: CustomizedBiCardPluginType['renderNode'] = createRenderNode(voteCardConfig);

  // @ts-ignore
  const onAction: CustomizedBiCardPluginType['onAction'] = createOnAction(voteCardConfig, copyNodes);
  return {
    cardType: 'vote',
    onAction,
    renderNode,
    onCangjieCopy: createOnCangjieCopy(),
    menubar: {
      menu: createMenubar(),
    },
    queries: createQueries(),
  };
};

const createBiVoteCardPlugin = cardFactory(createPlugin);

export default createBiVoteCardPlugin;
